.banner {
  width: 251px;
  height: 357px;
  background: #86d3f4;
  position: absolute;
  top: 220px;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.banner__subtitle {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 19px;
  letter-spacing: 0em;
  text-align: center;
  color: #000000;
  margin-left: 10px;
  margin-right: 10px;
  padding-top: 20px;
  max-width: 231px;
  margin-top: 0;
  margin-bottom: 0;
}

.banner__barcode {
  width: 126px;
  height: 126px;
  background-image: url(../../assets/images/qr-code.png);
  background-position: center;
  margin-top: 20px;
}

.banner__text {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0em;
  text-align: center;
  color: #000000;
  margin-left: 62.5px;
  margin-right: 62.5px;
  margin-top: 20px;
  margin-bottom: 0;
}
